import { messaging } from './firebase';
import { getToken } from 'firebase/messaging';

export default function swDev() {
    const isLocalhost = Boolean(
        window.location.hostname === 'localhost' ||
        window.location.hostname === '[::1]' ||
        window.location.hostname.match(/^127(?:\.(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)){3}$/)
    );

    if ('serviceWorker' in navigator) {
        const swUrl = `${process.env.PUBLIC_URL}/firebase-messaging-sw.js`;
        
        if (isLocalhost) {
            checkValidServiceWorker(swUrl);
        } else {
            registerValidSW(swUrl);
        }
    } else {
        console.log('Service Worker tidak didukung');
    }
}

function registerValidSW(swUrl) {
    navigator.serviceWorker
        .register(swUrl)
        .then((registration) => {
            console.log("Service Worker terdaftar:", registration);

            // Setup messaging untuk mendapatkan token setelah registrasi
            if (messaging) {
                getToken(messaging, {
                    vapidKey: 'BNEpg49G8cUE79CVjPNMO95PumxhYRpa8hXKY3y88oyNuOxR2E48ouW5XuDSl0304x2R6RDS3wrhDV5AsnjlcUg',
                    serviceWorkerRegistration: registration
                }).then((currentToken) => {
                    if (currentToken) {
                        console.log('Token FCM berhasil didapatkan:', currentToken);
                    } else {
                        console.log('Token FCM tidak tersedia');
                    }
                }).catch((err) => {
                    console.error('Gagal mendapatkan token FCM:', err);
                });
            }
        })
        .catch((error) => {
            console.error("Pendaftaran Service Worker gagal:", error);
        });
}

function checkValidServiceWorker(swUrl) {
    fetch(swUrl, {
        headers: { 'Service-Worker': 'script' }
    })
        .then((response) => {
            const contentType = response.headers.get('content-type');
            if (
                response.status === 404 ||
                (contentType != null && contentType.indexOf('javascript') === -1)
            ) {
                navigator.serviceWorker.ready.then((registration) => {
                    registration.unregister().then(() => {
                        window.location.reload();
                    });
                });
            } else {
                registerValidSW(swUrl);
            }
        })
        .catch(() => {
            console.log('Tidak ada koneksi internet. App berjalan dalam mode offline');
        });
}