import React from 'react'
import { useState, useEffect } from "react"
import {Navbar, Container, Nav} from "react-bootstrap"
import {Menu} from "../data/Data"
import { NavLink } from "react-router-dom"
import {Link} from "react-scroll"

const NavbarComponent = () => {
  const [changeColor, setChangeColor] = useState(false)

  const changeBgcolor = () => {
    if (window.scrollY > 10) {
      setChangeColor(true)
    }else {
      setChangeColor(false)
    }
  }

  useEffect(() => {
    changeBgcolor()
    window.addEventListener('scroll', changeBgcolor)
  })

return (
<div>                                       
    <Navbar fixed="top" className={ changeColor? "color-active" : " "} expand="lg">
  <Container fluid>
  <Navbar.Brand className="fs-5 fw-bold text-light">Fatch.</Navbar.Brand>
    <Navbar.Toggle className="bg-light" aria-controls="navbarScroll" />
    <Navbar.Collapse className="text-dark" id="navbarScroll">
    <Nav className="mx-auto my-2 my-lg-0" style={{ maxHeight: '200px' }} navbarScroll>
        {Menu.map((data) => {
            return (
                <div className="nav-link text-light" key={data.id}>
                    <Link className="link" to={data.path} smooth={true} duration={50}>{data.nama_menu}</Link>
                </div>
            )
        })}
      </Nav>
    </Navbar.Collapse>
  </Container>
</Navbar>
</div>
)
}

export default NavbarComponent

