import React from 'react'
import {Row, Col, Container, Card, Form, Button, Alert, ListGroup, ListGroupItem, Image} from "react-bootstrap"
import { useState, useEffect } from "react"
import { Pendidikan, skillFrontend, skillBackend, database, certificate } from "../data/Data"
import Photo from "../assets/fath.webp"
import axios from "axios"
import { useNavigate, NavLink } from "react-router-dom"
import { initializeMessaging, onMessageListener } from '../firebaseMessaging'

const HomePage = () => {
  const navigate = useNavigate()
  const [changeColorh1, setChangeColorh1] = useState(false)
  const [ changeCard, setChangeCard ]= useState(false)

  const [nama, setNama] = useState('')
  const [pesan, setPesan] = useState('')
  const [error, setError] = useState('');
  const [msg, setMsg] = useState('');

  const [fcmToken, setFcmToken] = useState(null);

  useEffect(() => {
    // Panggil fungsi initializeMessaging untuk mendapatkan token FCM
    const getToken = async () => {
      const token = await initializeMessaging();
      if (token) {
        setFcmToken(token);
      }
    };
    getToken();

    // Mendaftarkan listener untuk menerima pesan notifikasi
    onMessageListener().then((payload) => {
      console.log('Notifikasi diterima:', payload);
      // Proses payload notifikasi sesuai kebutuhan
    });
  }, []);

  const handleClick = () => {
    if (Notification.permission === 'granted') {
      // Jika izin notifikasi sudah diberikan, tampilkan notifikasi
      new Notification('Web Portofolio Fatih', {
        body: 'Halo selamat datang di web Portofolio fatih',
        icon: '../dists/img/mts.png',
      });
    } else {
      // Jika izin belum diberikan, minta izin
      Notification.requestPermission().then((permission) => {
        if (permission === 'granted') {
          new Notification('Web Portofolio Fatih', {
            body: 'Halo selamat datang di web Portofolio fatih',
            icon: '../dists/img/mts.png',
          });
        }
      });
    }
  };

  const addMessage = (event) => {
    event.preventDefault();

    const messageData = {
      id: Date.now(),
      nama: nama,
      message: pesan
    };

    saveMessageToDatabase(messageData);
    
    // Menampilkan pesan keberhasilan
    setMsg("Pesan berhasil disimpan!");
    
    // Reset input form setelah pengiriman
    setNama('');
    setPesan('');
  };

  const saveMessageToDatabase = (messageData) => {
    const dbRequest = indexedDB.open("messagesDB", 1);

    dbRequest.onupgradeneeded = (event) => {
      const db = event.target.result;
      if (!db.objectStoreNames.contains("messages")) {
        const store = db.createObjectStore("messages", { keyPath: "id" });
        store.createIndex("message", "message", { unique: false });
      }
    };

    dbRequest.onsuccess = (event) => {
      const db = event.target.result;
      const transaction = db.transaction("messages", "readwrite");
      const store = transaction.objectStore("messages");

      store.put(messageData);

      transaction.oncomplete = () => {
        console.log("Pesan berhasil disimpan ke IndexedDB");
      };

      transaction.onerror = (event) => {
        console.error("Gagal menyimpan pesan ke IndexedDB", event);
        setError("Gagal menyimpan pesan.");
      };
    };

    dbRequest.onerror = (event) => {
      console.error("Kesalahan saat membuka IndexedDB:", event);
      setError("Kesalahan saat membuka IndexedDB.");
    };
  };


  const changeBgcolorh1 = () => {
    if (window.scrollY < 100) {
      setChangeColorh1(true)
    }else {
      setChangeColorh1(false)
    }
  }

  const changeShowCard = () => {
    if(window.scrollY > 450 ){
      setChangeCard(true)
    }else {
      setChangeCard(false)
    }
  }

  useEffect(() => {
    changeShowCard()
    window.addEventListener('scroll', changeShowCard)
  })

  useEffect(() => {
    changeBgcolorh1()
    window.addEventListener('scroll', changeBgcolorh1)
  })

  return (
    <div className="homepage w-100 min-vh-100 bg-dark" id='home'>
        <header className="w-100 min-vh-100 d-flex align-items-center">
            <Container>
              <Row className="header-box d-flex align-items-center">
                <Col lg="6" className="content-box text-light">
                  <h1>Selamat Datang <br />di Website portofolio Fatih Muzaqi</h1>
                  <h5>Web Developer | Gamers</h5>
                  <button onClick={handleClick} className='btn'>Notifikasi</button>
                </Col>
                <Col lg="6" className="img-box">
                    <img src={Photo} alt="" />
                </Col>
              </Row>
            </Container>
        </header>
        
        <div className="pendidikan w-100 min-vh-100 align-items-center" id='pendidikan'>
          <Container>
            <Row>
              <Col>
                <h2 className="text-center fw-bold text-light">Riwayat Pendidikan</h2>
                <p className="text-center text-light">Riwayat pendidikan saya dari tahun 2008 - 2026</p>
              </Col>
            </Row>
            <Row className="row2">
              { Pendidikan.map((data) => {
                return (
                  <Col key={data.id} className="card-pendidikan d-flex justify-content-center">
                    <Card style={{ width: '21rem'}}>
                      <Card.Body>
                        <Image className='image' src={data.image} rounded />
                      </Card.Body>
                      <span className='bg-dark'>
                        <Card.Body >
                        <Card.Title className='text-light'>{data.sekolah}</Card.Title>
                        <Card.Text className='text-light'>{data.deskripsi}</Card.Text>
                        </Card.Body>
                      </span>
                    </Card>
                  </Col>
                )
              })}
            </Row>
          </Container>
        </div>

        <div id='sertificate'></div>
<br />
<div className='sertificate w-100 min-vh-100 d-flex flex-column align-items-center bg-light'>
  <h1 className='fw-bold text-center mb-5 mt-5'>Certificate</h1>
  <div id="carouselExample" className="carousel slide w-100">
    <div className="carousel-inner" >
      {certificate.map((data, index) => {
        return (
          <div style={{
            justifyContent: 'center',
            alignItems: 'center',
            height: '500px', // Mengatur tinggi item carousel agar gambar selalu berada di tengah
          }}

            key={index}
            className={`carousel-item ${index === 0 ? 'active' : ''}`}
          >
            {/* Gambar yang responsif */}
            <img
              src={data.folder}
              className="d-block w-100 img-fluid"
              alt={`Sertifikat ${index + 1}`}
              style={{
                objectFit: 'cover',
                maxHeight: '500px', maxWidth:'700px' // Membatasi tinggi gambar agar tidak terlalu besar
              }}
            />
          </div>
        );
      })}
    </div>

    {/* Tombol Previous */}
    <button
      className="carousel-control-prev"
      type="button"
      data-bs-target="#carouselExample"
      data-bs-slide="prev"
    >
      <span className="bg-dark carousel-control-prev-icon" aria-hidden="true"></span>
      <span className="visually-hidden">Previous</span>
    </button>

    {/* Tombol Next */}
    <button
      className="carousel-control-next"
      type="button"
      data-bs-target="#carouselExample"
      data-bs-slide="next"
    >
      <span className="bg-dark carousel-control-next-icon" aria-hidden="true"></span>
      <span className="visually-hidden">Next</span>
    </button>
  </div>
</div>



        <div className="skill w-100 min-vh-100 align-items-center bg-light" id='skill'>
            <Container>
              <Row className="d-block">
                <Col>
                  <h1 className="text-center fw-bold">My Skill</h1>
                  <p className="text-center">Lorem ipsum dolor sit amet consectetur adipisicing elit. Officiis tenetur saepe</p>
                </Col>
                </Row>
                <Row className='skil-box'>
                <h5>Frontend</h5>
                {skillFrontend.map((datafront)=> {
                  return(
                    <Col className='col-md-3 m-3' key={datafront.id}>
                      <img src={datafront.image} alt="" />
                      <h5>{datafront.skil}</h5>
                  </Col>
                  )
                })}
                <h5>Backend</h5>
                {skillBackend.map((databackend)=> {
                  return(
                    <Col className='col-md-3 m-3' key={databackend.id}>
                      <img src={databackend.image} alt="" />
                      <h5>{databackend.skil}</h5>
                  </Col>
                  )
                })}
                <h5>Database</h5>
                {database.map((databse)=> {
                  return(
                    <Col className='col-md-3 m-3' key={databse.id}>
                      <img src={databse.image} alt="" />
                      <h5>{databse.skil}</h5>
                  </Col>
                  )
                })}
                </Row>
            </Container>
        </div>
        <div className="msg-box w-100 min-vh-100">
        <Container className="text-light">
              <Row>
                <Col className="input-box mt-5">
                {msg && <Alert variant='success'>{msg}</Alert>}
                {error && <Alert variant="danger">{error}</Alert>}
                  <Form onSubmit={addMessage}>
                  <Form.Group className="mb-3">
                    <Form.Label><b>Masukan Nama : </b></Form.Label>
                    <Form.Control onChange={(e)=> setNama(e.target.value)} value={nama} type="text" placeholder="Masukan nama" />
                  </Form.Group>
                  <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                      <Form.Label><b>Kirim pesan untuk saya : </b></Form.Label>
                      <Form.Control onChange={(e)=> setPesan(e.target.value)} value={pesan} as="textarea"  placeholder="Kasih saya masukan / pesan / saran dll" rows={7} />
                      <Button className="mt-3" type="submit" variant="info">Kirim</Button>{' '}
                  </Form.Group>
                  </Form>
                </Col>
              </Row>
            </Container>

        </div>
    </div>
  )
}

export default HomePage
