import React from 'react'
import { useState, useEffect } from 'react'
import axios from 'axios'
import {Card, ListGroup, Container, Row, Col} from 'react-bootstrap'


const PesanPage = () => {
  const [message, setMessage] = useState([])

  const getMessage = async () => {
    const response = await axios.get('http://localhost:5000/message')
    setMessage(response.data)
  }

  useEffect(()=> {
    getMessage()
  },[])

  return (
    <div className='w-100 min-vh-100 bg-dark d-flex justify-content-center'>
        <Container>
          <Row>
          {message.map((data) => {
            return(
              <Col key={data.id}>
                <Card style={{ width: '18rem' }}>
                  <ListGroup variant="flush">
                    <ListGroup.Item><h5>{data.nama}</h5></ListGroup.Item>
                    <ListGroup.Item>{data.pesan}</ListGroup.Item>
                  </ListGroup>
              </Card>
            </Col>
            )
        })}
          </Row>
        </Container>
    </div>
  )
}

export default PesanPage
