import { getToken, onMessage } from "firebase/messaging";
import { messaging } from './firebase';

export const initializeMessaging = async () => {
    try {
        if (!('Notification' in window)) {
            console.log('Browser tidak mendukung notifikasi');
            return;
        }

        if (!messaging) {
            console.log('Messaging tidak tersedia');
            return;
        }

        const permission = await Notification.requestPermission();
        if (permission !== 'granted') {
            console.log('Izin notifikasi ditolak');
            return;
        }

        // Tunggu service worker ready
        await navigator.serviceWorker.ready;
        
        const currentToken = await getToken(messaging, {
            vapidKey: 'BNEpg49G8cUE79CVjPNMO95PumxhYRpa8hXKY3y88oyNuOxR2E48ouW5XuDSl0304x2R6RDS3wrhDV5AsnjlcUg'
        });

        if (currentToken) {
            console.log('Token FCM berhasil didapatkan:', currentToken);

            // Simpan token ke localStorage agar tidak didapatkan lagi
            localStorage.setItem('fcmToken', currentToken);
            return currentToken;
        } else {
            console.log('Tidak bisa mendapatkan token');
            return null;
        }
    } catch (error) {
        console.error('Error dalam initializeMessaging:', error);
        return null;
    }
};


export const onMessageListener = () =>
    new Promise((resolve) => {
        onMessage(messaging, (payload) => {
            resolve(payload);
        });
    });