import React, { useEffect } from 'react';
import { Routes, Route, useLocation } from 'react-router-dom';
import NavbarComponent from './components/NavbarComponent';
import FooterComponent from './components/FooterComponent';
import HomePage from './pages/Homepage';
import AboutPage from './pages/AboutPage';
import PendidikanPage from './pages/PendidikanPage';
import SkillPage from './pages/SkillPage';
import PesanPage from './pages/PesanPage';
import { initializeMessaging, onMessageListener } from './firebaseMessaging';

function App() {
  const location = useLocation();
  const isPesanPage = location.pathname === '/pesan';

  useEffect(() => {
      console.log('Memulai inisialisasi messaging...');
      
      // Initialize messaging
      let messagingCleanup = initializeMessaging()
          .then(token => {
              if (token) {
                  console.log('FCM Token:', token);
              }
          })
          .catch(err => console.error('Error:', err));

      // Set up message listener
      let messageListener = null;
      const setupMessageListener = async () => {
          try {
              const payload = await onMessageListener();
              console.log('Foreground message received:', payload);
              if (Notification.permission === 'granted') {
                  new Notification(payload.notification.title, {
                      body: payload.notification.body,
                      icon: payload.notification.icon
                  });
              }
          } catch (err) {
              console.log('Message listener error:', err);
          }
      };

      setupMessageListener();

      // Cleanup function
      return () => {
          if (messagingCleanup && typeof messagingCleanup.then === 'function') {
              messagingCleanup.then(cleanup => {
                  if (cleanup && typeof cleanup === 'function') {
                      cleanup();
                  }
              }).catch(() => {});
          }
          
          if (messageListener && typeof messageListener === 'function') {
              messageListener();
          }
      };
  }, []);


    return (
      <>
      {!isPesanPage && <NavbarComponent />}
      <Routes>
        <Route path='/' element={<HomePage />} />
        <Route path='/about' element={<AboutPage />} />
        <Route path='/pendidikan' element={<PendidikanPage />} />
        <Route path='/skil' element={<SkillPage />} />
        <Route path='/pesan' element={<PesanPage />} />
      </Routes>
      {!isPesanPage && <FooterComponent />}
    </>
    );
}

export default App;